.maincontent {
  display: flex;
  flex-direction: column;
}

/* Circle */
.timerstate {
  margin: 25px auto;
}
.timer {
  display: flex;
  width: 100%;
  position: relative;
}
.timerImage {
  margin: 0 auto;
}

/* Timer face */
.timerFaceBox {
  position: absolute;
  width: 100%;
  top: 116.5px;
  text-align: center;
}
.workCircleBox {
  margin: 0 8px;
}
.timerImage {
  padding: 0 10px;
}
.timerPath {
  transform: rotate(0.75turn);
  transform-origin: center;
}

/* Buttons */
.buttons {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.button {
  margin: 0 30px;
}
@media screen and (max-width: 600px) {
  .buttons {
    flex-direction: column;
  }
  .button {
    text-align: center;
    margin: -25px auto;
  }
}